import React from 'react'
import PropTypes from "prop-types"

const Card = ({ src, tag, name }) => (
    <div className="card">
        <div className="card-image">
            <figure className="image is-4by3">
                <img width="300" height="300" src={src} loading="lazy" />
            </figure>
        </div>
        <div className="card-content has-text-centered">
            <div className="media">
                <div className="media-content">
                    <p className="tag is-danger is-light is-6">{tag}</p>
                    <p className="title is-4 has-text-dark">{name}</p>
                </div>
            </div>

            <div className="content">
                <button className="button is-success">Buy Now</button>
            </div>
        </div>
    </div>
)

Card.propTypes = {
    src: PropTypes.string,
    tag: PropTypes.string,
    name: PropTypes.string,
    currentPrice: PropTypes.string,
    originalPrice: PropTypes.string
}

Card.defaultProps = {
    src: ``,
    tag: ``,
    name: ``,
    currentPrice: ``,
    originalPrice: ``
}


export default Card;